import React from 'react';
import styled from 'styled-components'
import { Section, SectionDivider, SectionText, SectionTitle } from '../styles/GlobalComponents'
import { DiFirebase, DiZend, DiReact } from 'react-icons/di';

const Technologies = () =>  (
    <Section id="tech">
      <SectionDivider/>
      <SectionTitle>Technologies</SectionTitle>
      <SectionText>
        I've worked with a range a technologies in the web development world.
        From Back-end To Design
      </SectionText>
      <List>
        <ListItem>
         <DiReact style={{color: '#fff', fontSize: '50px'}} />
          <ListContainer>
            <ListTitle>Front-End</ListTitle>
            <ListParagraph>
              Experiece with <br />
              React.js
            </ListParagraph>
          </ListContainer>
        </ListItem>
        <ListItem>
            <DiFirebase style={{color: '#fff', fontSize: '50px'}} />
          <ListContainer>
            <ListTitle>Back-End</ListTitle>
            <ListParagraph>
              Experience with <br />
              Node and Databases
            </ListParagraph>
          </ListContainer>
        </ListItem>
        <ListItem>
            <DiZend style={{color: '#fff', fontSize: '50px'}} />
          <ListContainer>
            <ListTitle>UI/UX</ListTitle>
            <ListParagraph>
              Experience with <br />
              tools like Figma
            </ListParagraph>
          </ListContainer>
        </ListItem>
      </List>
    </Section>
  );
  
export default Technologies;

export const List = styled.ul`
list-style-type: none;
display: grid;
grid-template-columns: repeat(3, 1fr);
gap: 40px;
margin: 3rem 0;

@media screen and (max-width: 768px){
margin: 64px 0;
gap: 24px
}
  
@media screen and (max-width: 480px){
display: flex;
flex-direction: column;
margin: 32px 0;
}
`

export const ListContainer = styled.div`
display: flex;
flex-direction: column;
`

export const ListTitle = styled.h4`
font-weight: 700;
font-size: 28px;
line-height: 32px;
letter-spacing: 0.02em;
color: #FFFFFF;
margin-bottom: 8px;

@media screen and (max-width: 768px){
font-size: 24px;
line-height: 28px;
}
`

export const ListParagraph = styled.p`
font-size: 18px;
line-height: 30px;
color: #fff;

@media screen and (max-width: 768px){
font-size: 16px;
line-height: 28px;
}
`

export const ListItem = styled.li`
max-width: 320px;
display: flex;
flex-direction: column;
align-items: center;

@media screen and (max-width: 768px){
max-width: 203px;
}
`

export const ListIcon = styled.div`
display: block;
width: 48px;
height: 48px;
margin-bottom: 10px;

@media screen and (max-width: 768px){
width: 40px;
height: 40px;
margin-bottom: 8px;
}
`

  