import styled from 'styled-components'
import { Link } from 'react-scroll'


export const Section = styled.section`
  display: flex;
  flex-direction: column;
  padding: 32px 48px 0;
  margin: 0 auto;
  max-width: 1400px;
  box-sizing: content-box;
  position: relative;
  overflow: hidden;
  grid-template-columns: 1fr 1fr;

  @media screen and (max-width: 768px) {
    padding: 24px 48px 0;
    flex-direction: column;
  }

  @media screen and (max-width: 640px) {
    padding: 16px 16px 0;
    width: calc(100vw - 32px);
    flex-direction: column;
  }
`

export const SectionTitle = styled.h2`
  color: #fff;
  font-weight: 800;
  font-size: 56px;
  line-height: 56px;
  width: max-content;
  max-width: 100%;
  margin-bottom: 16px;
  padding: ${(props) => props.main ? '58px 0 16px' : '0'};

  @media screen and (max-width: 768px){
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 12px;
    padding: 40px 0 12px;
  }

  @media screen and (max-width: 640px){
    font-size: 32px;
    line-height: 40px;
    font-size: 32px;
    line-height:40px;
    margin-bottom: 8px;
    padding: 16px 0 8px;
    max-width: 100%;
  }
`

export const SectionText = styled.p`
  max-width: 800px;
  font-size: 24px;
  line-height: 40px;
  font-weight: 300;
  padding-bottom: 3.6rem;
  color: #fff;

  @media screen and (max-width: 768px) {
    max-width: 670px;
    font-size: 20px;
    line-height: 32px;
    padding-bottom: 24px;
  }

  @media screen and (max-width: 640px) {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 16px;
  }
`

export const SectionDivider = styled.div`
display: block;
position: relative;
margin: 1rem 0;
width: 100vm;
height: 1px;
border: none;
background-size: 200% 100%;
background-image: linear-gradient(to right, #F46737, #945DD6, #13ADC7);
animation-duration: 2s;
animation-name: slide;
animation-iteration-count: infinite;
animation-direction: alternate;
  
&:delay {
animation-delay: 1s;
}
    
&::before, &::after {

display: block;
position: absolute;
width: 3px;
height: 3px;
top: -1px;
}
    
&::before {
left: 0;
top: -1px;
}
    
&::after {
right: 0;
}

@keyframes slide {
from { background-position: left 0; }
to { background-position: right 0; }
}
`
export const SectionSubText = styled.p`
max-width: 800px;
font-weight: 300;
font-size: 18px;
line-height: 32px;
color: #fff;

@media screen and (max-width: 768px) {
    max-width: 672px;
    font-size: 16px;
    line-height: 25px;
  }

  @media screen and (max-width: 640px) {
    font-size: 14px;
    line-height: 22px;
  }
`

export const Button = styled(Link)`
display: flex;
border: 0;
outline: 0;
padding: 7px 20px;
border-radius: 15px;
flex-wrap: wrap;
flex-direction: column;
justify-content: space-evenly;
align-items: center;
transition: all .3s ease-in-out;
background: linear-gradient(to right, #FEC163 0%, #DE4313 100%);
outline: none;
border: none;
cursor: pointer;
font-size: 1.2rem;
color: white;
box-shadow: 0 20px 30px -6px #131313;

`