import React from 'react'
import styled from 'styled-components'
import { projects } from '../constants/constants'
import { Section, SectionDivider, SectionTitle, Button } from '../styles/GlobalComponents'



const Project = () => {
return (
<Section fluid className="projects-section" id="projects">
    <SectionDivider/>
    <SectionTitle main>Projects</SectionTitle>
    <GridContainer>
      {projects.map((p, i) => {
        return (
          <BlogCard key={i}>
            <Img src={p.image} />
            <TitleContent>
              <HeaderThree title>{p.title}</HeaderThree>
              <Hr />
            </TitleContent>
            <CardInfo className="card-info">{p.description}</CardInfo>
            <div>
              <TitleContent>Stack</TitleContent>
              <TagList>
                {p.tags.map((t, i) => {
                  return <Tag key={i}>{t}</Tag>;
                })}
              </TagList>
            </div>
            <UtilityList>
              <Button href={p.visit}>Code</Button>
              <Button href={p.source}>Source</Button>
            </UtilityList>
          </BlogCard>
        );
      })}
    </GridContainer>
  </Section>
);
}
export default Project;


export const Img = styled.img`
width: 100%;
height: 100%;
object-fit: cover;
overflow: hidden;
`


export const GridContainer = styled.section`
display: grid;
grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
padding: 3rem;
place-items: center;
column-gap: 2rem;
row-gap: 3rem;
@media screen and (max-width: 460px) {
display: flex;
flex-direction: column;
padding: 2rem;
padding-bottom: 0;
}
`
export const BlogCard = styled.div`
border-radius: 10px;
box-shadow: 3px 3px 20px rgba(80, 78, 78, 0.5);
text-align: center;
width: 400px;
@media screen and (max-width: 460px) {
width: 100%;
}
`
export const TitleContent = styled.div`
color: #fff;
text-align: center;
z-index: 20;
width: 100%;
`
export const HeaderThree = styled.h3`
font-weight: 500;
letter-spacing: 1px;
color: #fff;
padding: .5rem 0;
font-size: 3rem;
`
export const Hr = styled.hr`
width: 50px;
height: 3px;
margin: 20px auto;
border: 0;
background: #d0bb57;
`
export const CardInfo = styled.p`
width: 100%;
padding: 0 50px;
color: #e4e6e7;
font-style: 2rem;
line-height: 24px;
text-align: justify;
@media screen and (max-width: 460px) {
padding: .3rem
}
`

export const UtilityList = styled.ul`
list-style-type: none;
padding: 0;
display: flex;
justify-content: space-around;
margin: 2.5rem 0;
`
export const ExternalLinks = styled.a`
color:#d4c0c0;
font-size: 1.6rem;
padding:1rem 1.5rem;
background: #6b3030;
border-radius: 15px;
transition: 0.5s;
&:hover{
  background: #801414;

}
`

export const TagList = styled.ul`
display: flex;
justify-content: space-around;
padding: 1rem;
`

export const Tag = styled.li`
color: #d8bfbf;
font-size: 1rem;
list-style-type: none;
`