import React from 'react'
import styled from 'styled-components';
import { AiFillGithub, AiFillInstagram, AiFillLinkedin } from 'react-icons/ai';

const Footer = () => {
    return (
    <FooterContainer id="footer">
      <FooterLinkList>
        <FooterLinkColumn>
          <FooterLinkTitle>Phone</FooterLinkTitle>
          <FooterLinkItem href="tel:+31638759328">+31 638 759 328</FooterLinkItem>
        </FooterLinkColumn>
        <FooterLinkColumn>
          <FooterLinkTitle>Email</FooterLinkTitle>
          <FooterLinkItem href="mailto:wwflorkiewicz@gmail.com">
            wwflorkiewicz@gmail.com
          </FooterLinkItem>
        </FooterLinkColumn>
      </FooterLinkList>
      <SocialIconsContainer>
        <TextContainer>
          <FooterText>Want to work together or have any questions?</FooterText>
        </TextContainer>
        <SocialContainer>
          <SocialIcons href="https://google.com">
            <AiFillGithub size="3rem" />
          </SocialIcons>
          <SocialIcons href="https://google.com">
            <AiFillLinkedin size="3rem" />
          </SocialIcons>
          <SocialIcons href="https://google.com">
            <AiFillInstagram size="3rem" />
          </SocialIcons>
        </SocialContainer>
      </SocialIconsContainer>
    </FooterContainer>
  );
};

export default Footer

export const SocialIcons = styled.a`
transition: 0.5s ease;
color: #fff;
border-radius: 50px;
padding: 9px;
&:hover {
background-color: #945DD6;
transform: scale(1.2);
cursor: pointer;
}
`;

export const FooterContainer = styled.div`
width: 100vw;
max-width: 1200px;
padding: 20px 50px 40px;
margin: 1.5rem auto;
box-sizing: content-box;
@media screen and (max-width: 640px) {
padding: 0 16px 48px;
width: 100vw;
}
`;

export const FooterLinkItem = styled.a`
font-size: 20px;
line-height: 40px;
color: #fff;
margin-bottom: 16px;
transition: .5s ease;
position: relative;
left: 0;

&:hover {
color: #945DD6;
left: 6px;
}
@media screen and (max-width: 768px) {
font-size: 17px;
line-height: 30px;
display: flex;
}
`;

export const SocialIconsContainer = styled.div`
max-width: 1200px;
display: flex;
justify-content: space-between;

@media screen and (max-width: 768px) {
display: flex;
justify-content: space-between;
}

@media screen and (max-width: 640px) {
display: flex;
width: 100%;
flex-direction: column;
}
`

export const TextContainer = styled.div`
display: flex;
align-items: baseline;
flex-wrap: wrap;
margin-right: auto;
@media screen and (max-width: 768px) {
flex-direction: column;
align-items: baseline;
}
@media screen and (max-width: 640px) {
display: flex;
flex-direction: column;
margin: 0 0 32px;
align-items: center;
}
`

export const FooterText = styled.p`
color: #fff;
min-width: 280px;
letter-spacing: 0.1rem;
font-size: 20px;
line-height: 30px;
padding: 1rem;

@media screen and (max-width: 768px) {
font-size: 16px;
line-height: 28px;
}
@media screen and (max-width: 640px) {
line-height: 22px;
font-size: 14px;
min-width: 100px;
}
`

export const SocialContainer = styled.div`
display: flex;
align-items: center;

@media screen and (max-width: 768px) {
justify-content: center;
padding-right: 16px;
flex-wrap: wrap;
}
`

export const FooterLinkList = styled.ul`
border-top: 1px solid rgba(255, 255, 255, 0.1);
display: grid;
grid-template-columns: repeat(3, minmax(85px, 220px));
gap: 40px;
padding: 40px 0 28px;

@media screen and (max-width: 768px) {
width: 100%;
padding: 32px 0 16px;
gap: 16px;
}
@media screen and (max-width: 640px) {
width: 100%;
padding: 32px 4px 16px;
gap: 5px;
}
`

export const FooterLinkColumn = styled.div`
display: flex;
flex-direction: column;
width: 100%;
`
export const FooterLinkTitle = styled.h4`
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 24px;
text-transform: uppercase;
color: #fff;
margin-bottom: 16px;
@media screen and (max-width: 640px) {
font-size: 15px;
line-height: 12px;
margin-bottom: 8px;
}
`