import React from 'react'
import styled from 'styled-components'
import { Section, SectionText, SectionTitle } from '../styles/GlobalComponents';

const About = () => {
return (
<Section id="about">
  <SectionTitle>About Me</SectionTitle>
  <SectionText>I’m Wioletta Florkiewicz, a front-end developer. I am passionate about leveraging my diverse background to decipher challenging problems and create delightful experiences. I honed my skills in web development.
    I develop websites with HTML, CSS, and JavaScript, and React. I have also skills in using opensource software like WordPress.
    And in my spare time I play back-end in Java and learning databases from microsoft sql
    </SectionText>
</Section>
);
};

export default About;

export const CarouselContainer = styled.ul`
  max-width: 1040px;
  background: #0F1624;
  padding: 0rem;
  list-style:none;
  display: flex;
  justify-content: space-between; 
 
`

export const CarouselItem = styled.div`
  background: #0F1624;
  border-radius: 3px;
  max-width: 196px;

  @media screen and (max-width: 768px) {
    max-width: 124px;
  }
  
  @media screen and (max-width: 640px) {
    margin-left: 32px;
    min-width: 120px;
    background: #0E131F;
    padding: 4px;
    align-content: start;
    scroll-snap-align: start;
    border-radius: 3px;
    overflow: visible;
    position: relative;
    height: fit-content;
  }
`

export const CarouselItemTitle = styled.h4`
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.02em;
  display: flex;
  /* This gradient is different due to the size of the Title container, it must transition sooner to be visible on the text */
  background: linear-gradient(121.57deg, #FFFFFF 10%, rgba(255, 255, 255, 0.66) 30.15%);
  margin-bottom: 8px;

  @media screen and (max-width: 768px) {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 4px;
  }
  
  @media screen and (max-width: 640px) {
    font-size: 16px;
    line-height: 24px;
  }
`

export const CarouselItemText = styled.p`
  font-size: 10px;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: rgba(255, 255, 255, 0.75);
  padding-right: 16px;

  @media screen and (max-width: 768px) {
    font-size: 12px;
    line-height: 18px;
    padding-right: 32px;
  }
  @media screen and (max-width: 640px) {
    font-size: 10px;
    line-height: 16px;
    padding-right: 0;
  }
`
export const CarouselButtons = styled.div`
  width: 288px;
  display: none;
  visibility: hidden;

  @media screen and (max-width: 640px) {
    display: flex;
    visibility: visible;
    margin-bottom: 48px;
  }
`

export const CarouselButton = styled.button`
  box-sizing: border-box;
  background: none;
  padding: 4px;
  border: none;
  cursor: pointer;
  margin-right: 4px;
  opacity: .33;
  transform: scale(1);

  &:focus {
    outline: none;
  }
`

export const CarouselButtonDot = styled.div`
  background-color: white;
  border-radius: 10px;
  margin: auto;
  width: 3px;
  height: 3px;
`
