import React from 'react'
import styled, {keyframes} from 'styled-components'
import { Button } from '../styles/GlobalComponents'
import Particle from '../styles/Particle'
import reacthero from '../../images/reacthero.svg'
import { SectionText, SectionTitle } from '../styles/GlobalComponents';
import HeroBg from '../../images/HeroBg.svg'


const move = keyframes`
0% { transform: translateY(-5px)  }
    50% { transform: translateY(10px) }
    100% { transform: translateY(-5px) }
`;


const Hero = () => {
const fadeLeft = {
    hidden: { opacity: 0, x: -100},
    visible: { opacity: 1, x: 0}
}
    return (
        
        <Section>
            <Particle/>
            <Container fluid className="home-section" id="home">
                <ColumnLeft>
                <SectionTitle
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 1 }}
                >Hi, I'm Meoffy 👋</SectionTitle>
                <SectionText
                variants={fadeLeft}
                initial='hidden'
                animate='visible'
                transition={{ duration: 1 }}
                >Junior Front-End Developer</SectionText>
                <Button>Hire me</Button>
                </ColumnLeft>
                <ColumnRight>
                <Image src={reacthero}/>
                </ColumnRight>
            </Container>
        </Section>
    )
};

export default Hero;

export const Image = styled.img`
max-width: 100%;
width: calc(30% + 20vw);
height: auto;
z-index: 7;
animation: ${move} 2.5s ease infinite;
@media only Screen and (max-width: 48em) {
align-self: flex-start;
position: absolute;
bottom: 0;
width: calc(30% + 20vw);
opacity: 0.5;
}
@media only Screen and (max-width: 40em) {
display: none;
}
`

export const ColumnRight = styled.div`
display: flex;
justify-content: center;
align-items: center;
padding: 2rem;
position: relative;
`

export const Section = styled.div`
position: relative;
z-index: -1;
display: flex;
flex-direction: column;
padding: 32px 48px 0;
max-width: 100%;
box-sizing: content-box;
overflow: hidden;
grid-template-columns: 1fr 1fr;
background-image: url(${HeroBg});
background-size: cover;

@media screen and (max-width: 768px) {
padding: 24px 48px 0;
flex-direction: column;
}

@media screen and (max-width: 640px) {
padding: 16px 16px 0;
width: calc(100vw - 32px);
flex-direction: column;
}
`

const Container = styled.div`
display: grid;
grid-template-columns: 1fr 1fr;
height: 100vh;
padding: 3rem cal((100vw - 1300px) / 2);
@media screen and (max-width: 768px) {
grid-grid-template-columns: 2fr;
}
`
const ColumnLeft = styled.div`
display: flex;
color: #fff;
flex-direction: column;
justify-content: center;
align-items: flex-start;
padding: 0 10rem;
`